export interface GetAutosnatchTargets {
  type: 'GetAutosnatchTargets';
}
export type GetAutosnatchTargetsResponse =
  | {
      hasDisabledProfiles: boolean;
      targetGroups: AutosnatchTargetOptionGroup[];
    }
  | undefined;

