/**
 * Make the background script run totp qr code detection for the sending tab.
 */
export interface DetectTotpQrSecretForTab {
  type: 'DetectTotpQrSecretForTab';
  rects: {
    left: number;
    right: number;
    bottom: number;
    top: number;
  }[];
  devicePixelRatio: number;
}
export type DetectTotpQrSecretForTabResponse =
  | {
      status: 'found';
      totpParameters: TotpParameters;
      x: number;
      y: number;
    }
  | { status: 'retryLater' }
  | { status: 'notFound' };

