/**
 * A message that can be sent to the runtime
 */
export type RuntimeMessage =
  | GetLogins
  | RequestSync
  | DataUpdate
  | UnlockStateUpdate
  | GetUser
  | GetAutosnatchTargets
  | LoginSnatched
  | UndoAutosnatch
  | MarkAutosnatchesSeen
  | SaveContentScriptState
  | GetContentScriptState
  | AddAchievements
  | SetGeneratedPasswordForUrl
  | UpdateAutosnatchLogin
  | ArchiveAddUsernameLogin
  | SendHeyproblemsReport
  | WebappClientCoreParametersExtracted
  | GetClientCoreParameters
  | IsClientOutdated
  | GetDisabledSiteSettingForUrl
  | SetDisabledSiteSettingForUrl
  | DetectTotpQrSecretForTab
  | GetAutosnatchSettings
  | SetAutosnatchSettings
  | SavePasswordGeneratorSettings
  | GetPasswordGeneratorSettings
  | WriteAuditlogEvent
  | RequestForceRefreshClientCoreParameters
  | GetUnlockDeviceType
  | GetOverrideLibrary
  | ReplaceOverrides
  | DeleteOverrides
  | ForceOverrideUpdate
  | SaveGlobalSearchState
  | SetGlobalSearchHeight
  | GlobalSearchOpenUrl
  | WaitForSenderTabRequestsSettled
  | GetGlobalSearchShortcut
  | OpenGlobalSearchWindow
  | OpenShortcutSettings
  | SetClearValueFromClipboardAfterTimeout
  | PingActivity
  | GetScreenSize
  | ClearValueFromClipboard
  | GetUnprotectedValue
  | ContentScriptModeChanged;

/**
 * A message that can be sent to the content script.
 */
export type ContentScriptMessage =
  | FillForm
  | UrlUpdate
  | DataUpdate
  | UnlockStateUpdate
  | FillActive
  | GetIsAutosnatchActive
  | OverridesUpdate
  | GetContentScriptMode
  | GetGlobalSearchShortcutResponse
  | TriggerWebauthnUnlockInIframe
  | ClearValueFromClipboard;

/**
 * A message that can be sent to/received by the background or the content script
 */
export type Message = RuntimeMessage | ContentScriptMessage;

type MessageType = Message['type'];

type MessageResponseMap = {
  GetLogins: GetLoginsResponse;
  GetUser: GetUserResponse;
  GetAutosnatchTargets: GetAutosnatchTargetsResponse;
  HasRecentLogin: boolean;
  GetContentScriptState: ContentScriptRestorationState | undefined;
  SetGeneratedPasswordForUrl: LoginId;
  SendHeyproblemsReport: boolean;
  // CCP contains Uint8Arrays which won't transfer on chrome when not encoded as json.
  GetClientCoreParameters: { clientCoreParametersJSON: string } | undefined;
  IsClientOutdated: boolean;
  GetDisabledSiteSettingForUrl:
    | { disabled: boolean; host: string }
    | { disabled: undefined; host: string | undefined };
  DetectTotpQrSecretForTab: DetectTotpQrSecretForTabResponse;
  GetAutosnatchSettings: GetAutosnatchSettingsResponse;
  GetPasswordGeneratorSettings: GetPasswordGeneratorSettingsResponse;
  GetIsAutosnatchActive: boolean;
  GetUnlockDeviceType: UnlockDeviceType;
  GetOverrideLibrary: OverrideLibrary;
  GetContentScriptMode: ContentScriptMode | undefined;
  GetGlobalSearchShortcut: GetGlobalSearchShortcutResponse;
  TriggerWebauthnUnlockInIframe: string | undefined;
  GetScreenSize: ScreenSize;
  GetUnprotectedValue: UnprotectedValue | undefined;
};

export type MessageResponse<M extends Message> = M['type'] extends keyof MessageResponseMap
  ? MessageResponseMap[M['type']]
  : undefined | void;

// We wrap all message responses in an envelope. The extension API represents no response as
// undefined (and sometimes also randomly returns null). Wrapping responses in an envelope allows us
// to reliably distinguish these cases.
type MessageResponseEnvelope<T> = { value: T };

function isMessageResponseEnvelope(response: unknown): response is MessageResponseEnvelope<any> {
  return typeof response === 'object' && response !== null;
}

type MessageHandler<M extends Message> = (
  msg: M,
  sender: browser.Runtime.MessageSender,
) => Promise<MessageResponse<M>> | MessageResponse<M>;

type MessageFromType<T extends MessageType> = Extract<Message, { type: T }>;

export const messageError = Symbol('messageError');

export async function sendMessage<M extends RuntimeMessage>(
  m: M,
): Promise<MessageResponse<M> | typeof messageError> {
  return doSendMessage(() => browser.runtime.sendMessage(m));
}

export async function sendMessageToTab<M extends ContentScriptMessage>(
  tabId: number,
  m: M,
  frameId?: number,
): Promise<MessageResponse<M> | typeof messageError> {
  return doSendMessage(() => browser.tabs.sendMessage(tabId, m, { frameId }));
}

async function doSendMessage(send: () => Promise<any>) {
  try {
    const response = await send();
    if (response === null || response === undefined) {
      // We sometimes get stray null results when messages can't be sent anymore. We get undefined
      // if there's no handler for the given message type at the moment. Don't report, but return as
      // an error for upstream.
      return messageError;
    }
    if (isMessageResponseEnvelope(response)) {
      return response.value;
    }
    trackError(
      new Error(`Received unexpected response sending message: ${JSON.stringify(response)}`),
    );
    return messageError;
  } catch (e) {
    reportMessageError(e);
    return messageError;
  }
}

export async function sendMessageToAllTabs<M extends ContentScriptMessage>(
  m: M,
): Promise<MessageResponse<M>[]> {
  const tabs = await browser.tabs.query({});
  const tabMessages = tabs.map((t) =>
    t.id ? sendMessageToTab(t.id, m) : Promise.resolve(messageError),
  );
  const results = await Promise.allSettled(tabMessages);
  return results.flatMap((r) => {
    if (r.status !== 'fulfilled') {
      return [];
    }
    const { value } = r;
    return value !== messageError ? [value] : [];
  });
}

export async function broadcastMessage<M extends ContentScriptMessage & RuntimeMessage>(m: M) {
  return Promise.allSettled([sendMessage(m), sendMessageToAllTabs(m)]);
}

export type ComposedMessageListener = (
  message: Message,
  sender: browser.Runtime.MessageSender,
) => Promise<MessageResponseEnvelope<MessageResponse<Message>>> | undefined;

export function makeMessageListener(
  handlers: {
    [T in MessageType]?: MessageHandler<MessageFromType<T>>;
  },
): ComposedMessageListener {
  // CAUTION, don't make the message listener async! Doing that will lead to message flakiness and
  // hard to reproduce bugs. If there's multiple message listeners registered only one of them is
  // allowed to send a response. Browsers seem to take the listener that responds with a truthy
  // value. Since async functions always return a promise the every registered listener will try to
  // respond. Refer to the following links for more info:
  //
  // - https://developer.mozilla.org/en-US/docs/Mozilla/Add-ons/WebExtensions/API/runtime/onMessage#sect5
  // - https://issues.chromium.org/issues/40753031#comment11
  return <M extends Message>(msg: M, sender: browser.Runtime.MessageSender) => {
    const handler = handlers[msg.type] as MessageHandler<M> | undefined;
    if (handler) {
      try {
        return (async (): Promise<MessageResponseEnvelope<MessageResponse<M>>> => {
          const value = await handler(msg, sender);

          // Response is wrapped in an envelope, to allow us to distinguish messages where the
          // response is undefined from situations where there is no handler. Without the envelope
          // these would both be represented as plain undefined.
          return { value };
        })();
      } catch (e) {
        reportMessageError(e);
      }
    }
    // We return undefined here to signal that we don't have a handler for this message type. If
    // there's another message handler registered somewhere that handles this message type the
    // browser will prefer that response and return it to the message sender. Note that if all
    // handlers return undefined, the undefined response will be propagated to the message sender.
    // That case is handled in sendMessage/sendMessageToTab above.
    return undefined;
  };
}

const IGNORED_ERROR_MESSAGES: WebExtErrorType[] = [
  // happens when the other side goes away unexpectedly (e.g. tab close)
  'COULD_NOT_ESTABLISH_CONNECTION',
  // for urls on which extensions are never allowed to run (e.g. chrome://*, some about:* pages)
  'MISSING_HOST_PERMISSION_FOR_TAB',
  // Sending message to tab that was closed in the meantime
  'TAB_CLOSED',
  'NO_TAB_WITH_ID', // Chrome
  'INVALID_TAB_ID', // Firefox
  // Should be handled by polyfill, but isn't - https://github.com/mozilla/webextension-polyfill/issues/384
  'CLOSED_BEFORE_RESPONSE_RECEIVED',
  // Seems to happen in Firefox for some messages: https://sentry.heylogin.dev/organizations/heylogin/issues/755
  'UNEXPECTED_ERROR',
  // No need to report this one.
  // FIXME: When this error comes up, the content script is no longer in a functional state and we
  // might as well unload it entirely. This happens e.g. with lingering content scripts after the
  // extension is unloaded/reloaded.
  'CONTEXT_INVALIDATED',
];

function reportMessageError(e: unknown) {
  if (isWebExtError(e, ...IGNORED_ERROR_MESSAGES)) {
    return;
  }
  trackError(new Error('Failed to send message', { cause: e }));
}
