export type WebExtErrorType = keyof typeof ERRORS;

export function isWebExtError(obj: unknown, ...errorTypes: WebExtErrorType[]) {
  const message = getMessage(obj);
  if (message === undefined) {
    return false;
  }
  return errorTypes.some((errorType) => {
    const matcher = ERRORS[errorType];
    return typeof matcher === 'string' ? matcher === message : matcher.test(message);
  });
}

function hasMessageProperty(obj: unknown): obj is { message: string } {
  return (
    typeof obj === 'object' && obj !== null && 'message' in obj && typeof obj.message === 'string'
  );
}

// Most browsers use an object with a `message` property as an error, but Orion returns a plain
// string. We handle both cases here
function getMessage(obj: unknown) {
  if (typeof obj === 'string') {
    return obj;
  }
  if (hasMessageProperty(obj)) {
    return obj.message;
  }
  return undefined;
}

/**
 * Web extension errors are stringly typed and can also differ between browsers. We keep this object
 * of error messages we're interested in for easier matching.
 */
const ERRORS = {
  // Happens in content scripts on sending messages, after the extension has been unloaded (e.g.
  // during update or uninstall).
  CONTEXT_INVALIDATED: 'Extension context invalidated.',
  TAB_DRAG_IN_PROGRESS: 'Tabs cannot be edited right now (user may be dragging a tab).',
  COULD_NOT_ESTABLISH_CONNECTION: 'Could not establish connection. Receiving end does not exist.',
  MISSING_HOST_PERMISSION_FOR_TAB: 'Missing host permission for the tab',
  TAB_CLOSED: 'The tab was closed.',
  NO_TAB_WITH_ID: /^No tab with id: \d+\.$/, // Chrome
  INVALID_TAB_ID: /^Invalid tab ID: \d+$/, // Firefox
  ACTIVE_TAB_PERMISSION_NOT_IN_EFFECT:
    "The 'activeTab' permission is not in effect because this extension has not been in invoked.",
  CAPTURE_TAB_IMAGE_READBACK_FAILED: 'Failed to capture tab: image readback failed',
  CANNOT_ACCESS_CONTENTS_OF_URL:
    'Cannot access contents of url "". Extension manifest must request permission to access this host.',
  ALL_URLS_OR_ACTIVE_TAB_PERMISSION_REQUIRED:
    "Either the '<all_urls>' or 'activeTab' permission is required.",
  UNEXPECTED_ERROR: 'An unexpected error occurred',
  ERROR_CAPTURING_VISIBLE_TAB: 'Error capturing visible tab.',
  INVALID_VALUE_FOR_BOUNDS:
    'Invalid value for bounds. Bounds must be at least 50% within visible screen space.',
  CLOSED_BEFORE_RESPONSE_RECEIVED:
    'A listener indicated an asynchronous response by returning true, but the message channel closed before a response was received',
  PERMISSION_ERROR:
    "Invalid call to tabs.captureVisibleTab(). Either the 'activeTab' permission or granted host permissions for the current website are required.",
} as const;
